.TaskEditor {
    display: flex;
    flex-direction: column;
    padding: 12px;
    width: 375px;
    border-radius: 10px;
    box-shadow: 1px 2px 8px  rgb(39, 36, 36);
    margin-bottom: 16px;
  }
  
  .TaskEditor_label {
    display: inline-block;
    margin-bottom: 16px;
    font-size: 20px;
    color: #171718;
    cursor: pointer;
  }
  
  .TaskEditor_input {
    color: #171718;
    font: inherit;
    font-size: 1.2rem;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid #9ea0bb;
    width: 350px;
    outline: 0;
  }
  
  .TaskEditorinput:focus {
    border: 1px solid #2b32b2;
  }
  
  .TaskEditor_button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background-color: #3f51b5;
    border: 0;
    outline: 0;
    padding: 8px 24px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 1px 2px 8px  rgb(39, 36, 36);
    font: inherit;
    text-transform: uppercase;
    user-select: none;
    transition: background-color 200ms ease-in-out;
    width: 374px;
  }
  
  .TaskEditor_button:hover {
    box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.15);
    background-color: #303f9f;
  }